import { AppBar, Card, Paper, Tab, Tabs } from "@material-ui/core";
import React, { useState } from "react";
import styled from "styled-components";
import renderTemplate from "./renderTemplate";

const Root = styled.div`
    font-family: HindMRegular;
`;

const StyledHeader = styled.div`
    h2 {
        font-size: 18px;
        line-height: 24px;
    }
    hr {
        margin-bottom: 24px;
    }
`;

const ComponentContainer = styled.div`
    margin-top: 40px;
`;

const TabPanel = styled(Card)`
    margin-top: 12px;
    padding: 8px 8px 40px;
`;

const DocumentationContainer = styled(TabPanel)`
    .propTable {
        td {
            padding: 6px 8px;
        }
        tr > td:first-child {
            font-weight: bold;
        }
    }
`;

const StoryContainer = ({
    title,
    description,
    component,
    examples,
    documentation,
}) => {
    const [tabValue, setTabValue] = useState("component");
    const renderHeader = () => {
        if (!title) {
            return;
        }

        return (
            <StyledHeader>
                <h1>
                    {title}
                </h1>
                <hr />
                <h2>
                    {description}
                </h2>
            </StyledHeader>
        );
    };

    const changeTab = (ev, newValue) => {
        setTabValue(newValue);
    };

    const renderComponent = () => {
        if (!component || tabValue !== "component") {
            return;
        }

        return (
            <TabPanel>
                {renderTemplate(component)}
            </TabPanel>

        );
    };

    const renderExamples = () => {
        if (!examples || tabValue !== "examples") {
            return;
        }

        return (
            <TabPanel>
                {renderTemplate(examples)}
            </TabPanel>

        );
    };

    const renderDocumentation = () => {
        if (!documentation || tabValue !== "documentation") {
            return;
        }

        return (
            <DocumentationContainer>
                {/* dont use core components inside! */}
                {documentation}
            </DocumentationContainer>
            
        );
    };

    const rendercontainer = () => {
        return (
            <ComponentContainer>
                <Tabs
                    value={tabValue}
                    indicatorColor="primary"
                    textColor="primary"
                    aria-label="disabled tabs example"
                    onChange={changeTab}
                >
                    {component && <Tab label="Component" value="component" />}
                    {examples && <Tab label="Examples" value="examples" />}
                    {documentation && <Tab label="Documentation" value="documentation" />}
                </Tabs>
                {renderComponent()}
                {renderExamples()}
                {renderDocumentation()}
            </ComponentContainer>
        );
    };

    return (
        <Root>
            {renderHeader()}
            {rendercontainer()}
        </Root>
    );
};

export default StoryContainer;
